<template>
  <div
  class="seat"
    :style="{
      left: 24 * (+item.columnNum+seatInterval/2-1) + 'px',
      top: 24 * (+item.rowNum) + 'px'
    }"
  >
  
    <svg
      class="icon"
      aria-hidden="true"
      width="100%"
      height="100%"
      :index="index"
    >
    
      <use :xlink:href="seatIcon"></use>
    </svg>
  </div>
</template>
<script>
import "@/assets/font_ia27i42ocvd/iconfont.js";
export default {
  props: {
    item: { type: Object, require: true },
    index: { type: Number, require: true },
    seatInterval:{type:Number,default:0}
  },
  mounted(){
  },
  computed: {
    seatIcon() {
      if (this.item.isBroken) {
        return "#icon-chupiaosuoding";
      } else if (this.item.isOccupied) {
        return "#icon-yishou";
      } else if (this.item.isChoose) {
        return "#icon-xuanzhong";
      } else {
        return "#icon-kexuanzuowei";
      }
    },
  },
};
</script>
<style scoped>
.seat{
  transition: all .4s ease;
}
</style>