<template>
  <div class="seating-chart-wrap" v-if="seatingChart">
    <div class="screen"><slot></slot></div>
    <div class="rowNav" :style="{
      height: `${seatingChart.height * 24}px`,
      transform: `translateY(${tsfY + 24 * mapScale}px) scaleY(${mapScale})`,
    }">
      <div v-for="item in seatingChart.height" :key="item">{{ item }}</div>
    </div>
    <div class="seating-chart" :style="{
      width: `${(seatingChart.width + seatInterval) * 24}px`,
      height: `${(seatingChart.height + seatInterval) * 24}px`,//这里的数字为了在底部流出空隙

    }" @click="chooseSeat($event)" ref="seatMap">
      <Seat-icon v-for="(item, index) in seatingChart.seats" :key="index" :item="item" :index="index"
        :seatInterval="seatInterval" class="seatBox">
      </Seat-icon>
    </div>
  </div>
</template>
<script>
import { EasyScroller } from "easyscroller";
import { Toast } from "vant";
import http from "@/util/http";
import SeatIcon from "@/components/schedule/seatMap/component/SeatIcon.vue";
export default {
  components: { SeatIcon },
  props: {
    maxSeat: { type: Number, require: true },
  },
  component: {
    SeatIcon,
  },
  data() {
    return {
      seatingChart: null,
      tsfY: 0,
      mapScale: 0,
      seatInterval: 6,
      scroller: null,
    };
  },
  mounted() {
    this.getSeatingChart(this.$route.params.scheduleId);
  },
  methods: {
    getSeatingChart(scheduleId) {
      http({
        url: `/gateway/?scheduleId=${scheduleId}&k=4878872`,
        headers: {
          "X-Host": "mall.film-ticket.seat.list",
        },
      }).then((res) => {
        this.seatingChart = res.data.data.seatingChart;
        this.mapScale =
          document.documentElement.clientWidth /
          (this.seatingChart.width + this.seatInterval) /
          24;
        this.$nextTick(() => {
          this.refreshMap()
          this.observeSeatMap();
        });
      })
    },
    observeSeatMap() {
      let parent = this.$refs.seatMap;
      const operate = (matrix) => {
        this.mapScale = +(matrix[0]);
        this.tsfY = +(matrix[5]);
      }
      const observer = new MutationObserver(function (mutations) {
        mutations.forEach((mutation) => {
          if (mutation.attributeName === "style") {
            var matrix = window
              .getComputedStyle(parent)
              .getPropertyValue("transform")
              .slice(7, -2)
              .split(",");
            operate(matrix)
          }
        });
      });
      observer.observe(parent, { attributes: true });
    },
    chooseSeat(e) {
      let index = 0;
      if (e.target.tagName === "svg" || e.target.parentNode.tagName === "svg") {
        index =
          e.target.getAttribute("index") ??
          e.target.parentNode.getAttribute("index");
      } else {
        return;
      }
      const isBroken = this.seatingChart.seats[index].isBroken;
      const isOccupied = this.seatingChart.seats[index].isOccupied;
      const seat = this.seatingChart.seats[index];
      //未被占用and未损坏
      if (!isBroken && !isOccupied) {
        if (!seat.hasOwnProperty("isChoose")) {
          this.$set(seat, "isChoose", false);
        }
        // 用户点击了被选中的座位
        if (seat.isChoose) {
          this.$store.commit("chosenDeleteOne", seat)
        } else {
          // 空的座位 超美超过每个人的最大选择限制
          if (this.$store.state.chosen.length >= this.maxSeat) {
            Toast(`最多只能选择${this.maxSeat}个座位`);
            return;
          }
          this.$store.commit("chosenPushOne", seat)
        }
      }
    },
    refreshMap() {
      if (this.scroller) {
        this.scroller.destroy()
      }
      let _this = this;
      let element = document.querySelector(".seating-chart");
      this.scroller = new EasyScroller(element, {
        // scrollingX: true,
        // scrollingY: true,
        zooming: true,
        minZoom: _this.mapScale,
        maxZoom: 2.0,
        zoomLevel: _this.mapScale,
        // animating: true,
      })
    },
  },
};
</script>
<style scoped lang="scss">
.seating-chart-wrap {
  padding-top: 10%;
  background-color: #f6f6f6;
  position: relative;
  overflow: hidden;
  // overflow: scroll !important;
  // scroll-behavior: ;
  height: calc(100vh - 189px);
}

.seating-chart {
  position: relative;

  .seatBox {
    position: absolute;
    height: 1.125rem;
    width: 1.5rem;
    display: inline-block;
    padding: 0.0938rem;
  }
}

.rowNav {
  transform-origin: 0 0;
  min-width: .5rem;
  max-width: 1rem;
  position: absolute;
  left: .5em;
  background: #000;
  opacity: 0.2;
  display: flex;
  flex-direction: column;
  color: #fff;
  border-radius: 0.4688rem;

  div {
    transform: scaleY(1);
    text-align: center;
    font-size: 0.75rem;
    height: 2.25rem;
    line-height: 24px;
    z-index: 99999;
  }
}

.screen {
  height: 1.125rem;
  width: 9.375rem;
  position: absolute;
  top: 0;
  text-align: center;
  background: #000;
  opacity: 0.2;
  color: #fff;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 0 0 0.3125rem 0.3125rem;
  font-size: 0.75rem;
}
</style>